import * as React from 'react';
import { Component } from 'react';
import '../../style/Component/ExpDetails.scss'

function ExpDetails(props: any) {
    const {items} = props
    return (
        <div className="block-exp_items">
            <div className="block-exp_items-wrapper">
                <div className="block-exp_title">
                    <p>{items.List_title}</p>
                </div>
                <div className="block-exp_time">
                    <p>{items.List_time}</p>
                </div>
                <div className="block-exp_company">
                    <p>{items.List_company}</p>
                </div>
                <div className="block-exp_details">
                    <p>{items.List_details}</p>
                </div>
            </div>
        </div>
    );
  }
  
  export default ExpDetails;