import * as React from 'react';
import { Component } from 'react';
import  '../../style/Component/SkillPercent.scss';

function SkillPercent(props: any) {
    const {items} = props

    const [skill, setHover] = React.useState("")

    const MouseOver = (event:any) =>{
        setHover("active")
    }
    const MouseLeave = (event:any) =>{
        setHover("")
    }

    
    return (
        <div className="block-skill_items">
            <div className="block-skill_wrapper">
                <div className="items-name block-skill_name">
                    <p>{items.List_name}</p>
                </div>
                <div className="items-decs block-skill_percent">
                    <div className={`skill-percent ${skill}`} 
                    onMouseEnter={MouseOver}
                    onMouseLeave={MouseLeave}>
                        <svg viewBox="0 0 36 36" className="circle-chart blue">
                            <path className="circle-bg"
                                d="M18 2.0845
                                a 15.9155 15.9155 0 0 1 0 31.831
                                a 15.9155 15.9155 0 0 1 0 -31.831"
                            />
                            <path className="circle-round"
                                stroke-dasharray={`${items.List_percent}, 100`} 
                                d="M18 2.0845
                                a 15.9155 15.9155 0 0 1 0 31.831
                                a 15.9155 15.9155 0 0 1 0 -31.831"
                            />
                            <text x="18" y="20.35" className="percentage">{items.List_percent}</text>
                        </svg>
                    </div>
                </div>
            </div>
        </div>
    );
  }
  
  export default SkillPercent;