import * as React from "react";
import { useRef, useEffect } from "react";
import Aboutitems from "../../Component/InforItems";
import "../../../style/Layout/HomePage/Home_5.scss";
import bannerImage from "../../../images/png_ducphu.png";
import { gsap } from "gsap";
import { Navigation, Pagination, Scrollbar, A11y } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";

import ExpDetails from "../../Component/ExpDetails";

function Home_5(props: any) {
  const { Exp, indexPage, loading } = props;
  const tl: any = gsap.timeline();

  function clear() {
    tl.clear();
  }

  function aniIn() {
    clear();

    gsap.to(".dp-exp_title", {
      scale: 1,
      opacity: 1,
      duration: 0.5,
      ease: "power2.out",
      delay: 0.3,
    });
    gsap.to(".block-exp_items", {
      x: 0,
      opacity: 1,
      duration: 0.4,
      stagger: 0.1,
      ease: "power2.out",
      delay: 0.5,
    });
  }
  function aniOut() {
    clear();

    gsap.to(".dp-exp_title", {
      scale: 1.3,
      opacity: 0,
      duration: 0.1,
    });
    gsap.to(".block-exp_items", {
      x: 100,
      opacity: 0,
      duration: 0.1,
    });
  }
  useEffect(() => {
    aniOut();
    setTimeout(function () {
      aniIn();
    }, 300);
  }, [loading]);

  useEffect(() => {
    if (indexPage === 5) {
      aniIn();
    }
    if (indexPage === 4) {
      aniOut();
    }
  }, [indexPage]);

  return (
    <div className="section Home_5">
      <div className="Home_5-wrapper container">
        <div className="Home_5-wrapper_img">
          <img alt="me" src={bannerImage} />
        </div>
        <div className="Home_5-wrapper_infor">
          <div className="dp-exp">
            <div className="dp-exp_title title-header">
              <p>Experience.</p>
            </div>
            <div className="dp-exp_details">
              <Swiper
                modules={[Navigation]}
                navigation
                className="myExpSwiper"
                slidesPerView={1}
                spaceBetween={20}
                breakpoints={{
                  // when window width is >= 1366px
                  1366: {
                    slidesPerView: 2,
                    spaceBetween: 50,
                  },
                }}
              >
                {Exp.map((items: any, index: any) => (
                  <SwiperSlide key={index}>
                    <ExpDetails items={items} key={index} />
                  </SwiperSlide>
                ))}
              </Swiper>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home_5;
