import * as React from 'react';
import { Component } from 'react';
import  '../../style/Component/InforItems.scss';

function InforItems(props: any) {
    const {item} = props

    
    return (
        <div className="information__wrapper--list_items ">
            <div className="items">
                <div className="items-name title-color-bottom">
                    <p>{item.List_title}</p>
                </div>
                <div className="items-decs">
                    <p>{item.List_decs}</p>
                </div>
            </div>
        </div>
    );
  }
  
  export default InforItems;