import * as React from 'react'
import { useRef, useEffect,useState } from 'react'
import '../../../style/Layout/HomePage/Home_1.scss'
import { gsap } from 'gsap'

import bannerImage from '../../../images/png_ducphu 2_.png'

function Home_1(props: any) {
  const { content, indexPage,loading } = props
  const [hours, setHours] = useState(0)
  const [minutes, setMinutes] = useState(0)
  const [seconds, setSeconds] = useState(0)

  function getRealTime() {
    const currentTime = Date.now();
    const hour = new Date(Math.round(currentTime / 1000) * 1000).getHours()
    const minute = new Date(Math.round(currentTime / 1000) * 1000).getMinutes()
    const second = new Date(Math.round(currentTime / 1000) * 1000).getSeconds()
    const timeLeft = (Math.round(currentTime / 1000) + 1) * 1000 - currentTime
    return {timeLeft, hour,minute,second} ;
  }

  (async function () {
    let reduceTime = 0;
    while (true) {
      const time = getRealTime()
      reduceTime = time.timeLeft;
      await sleep(reduceTime);
      setHours(time.hour)
      setMinutes(time.minute)
      setSeconds(time.second)
    }
  })()
  
  function sleep(ms:any) {
    const result = new Promise(resolve => {
      setTimeout(resolve, ms)
    });
    return result
  }
  

  const tl: any = gsap.timeline()

  function clear() {
    tl.clear()
  }

  function aniIn() {
    clear()

    
      gsap.to('.banner-section__decs--infor_title', {
        y: 0,
        opacity: 1,
        duration: 0.3,
        delay: 0.3
      })
      gsap.to('.banner-section__decs--infor_decs', {
        y: 0,
        opacity: 1,
        duration: 0.3,
        delay: 0.5
      })
      gsap.to('.banner-section__decs--infor_time', {
        y: 0,
        opacity: 1,
        duration: 0.3,
        delay: 0.7
      })
      gsap.to('.banner-section__img', {
        opacity: 1,
        duration: 0.7,
        delay: 0.3
      })

      
  }
  function aniOut() {
    clear()
      gsap.to('.banner-section__decs--infor_title', {
        y: 100,
        opacity: 0,
        duration: 0.1,
      })
      gsap.to('.banner-section__decs--infor_decs', {
        y: 100,
        opacity: 0,
        duration: 0.1,
      })
      gsap.to('.banner-section__decs--infor_time', {
        y: 100,
        opacity: 0,
        duration: 0.1,
      })
      gsap.to('.banner-section__img', {
        opacity: 0,
        duration: 0.1,
      })
      
  }

  useEffect(()=>{
    aniOut()
    setTimeout(
        function (){
          aniIn()
        }
    , 300)
  },[loading])

  useEffect(() => {
    if (indexPage === 1) {
      aniIn()
    }
    if (indexPage === 2) {
      aniOut()
    }
  }, [indexPage])
  return (
    
    <div  className="section Home_1">
      <div className='arrow-down'>
        <svg xmlns="http://www.w3.org/2000/svg" width="36" height="35" viewBox="0 0 36 35">
          <g id="Group_3" data-name="Group 3" transform="translate(36.182 36.051) rotate(180)">
            <g id="Rectangle_399" data-name="Rectangle 399" transform="translate(0.182 1.051)" fill="none" stroke="#ffc107" stroke-width="4">
              <rect width="36" height="35" rx="17.5" stroke="none"/>
              <rect x="2" y="2" width="32" height="31" rx="15.5" fill="none"/>
            </g>
            <path id="Path_10913" data-name="Path 10913" d="M0,0,6.844,6.057,0,12.064" transform="translate(12.035 21.602) rotate(-90)" fill="none" stroke="#ffc107" stroke-linecap="round" stroke-linejoin="round" stroke-width="4"/>
          </g>
        </svg>
      </div>
      <section id="banner">
        <div className="banner">
          <div className="banner-section container">
            <div className="wrapper">
              <div className="banner-section__decs column-6 column-12-ts">
                <div className="banner-section__decs--infor">
                  <div className="banner-section__decs--infor_title">
                    <p>Hi.</p>
                  </div>
                  <div className="banner-section__decs--infor_decs">
                    <p>I'm Phu, i'm a deverloper</p>
                  </div>
                  <div className="banner-section__decs--infor_time">
                    {hours}:{minutes}:{seconds}
                  </div>
                </div>
              </div>
              <div className="banner-section__img ">
                <div className="img"><img src={bannerImage} alt=""></img></div>
              </div>
            </div>
          </div>
        </div>
		</section>
    </div>
  )
}

export default Home_1
