import * as React from 'react';
import {useEffect,useState} from 'react';
import { Component } from 'react';
import  '../../style/Component/LoadingPage.scss';

function LoadingPage(props: any) {
    const {loading} = props
    
    const [Loaded, setLoaded] = useState("") 
   

    useEffect(()=>{
        if(loading === true){
            setLoaded("loaded")
        }
        
    },[loading])

    
    return (
        <div className={`loading-page ${Loaded}`}>
            <div className="items">
                <div className="header-wrapper__logo">
                    <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 121.415 106.011">
                        <g id="Group_60" data-name="Group 60" transform="translate(-132.455 -46.709)">
                        <g id="Group_3" data-name="Group 3" transform="translate(28 -5)">
                        <path id="Logo_1" data-name="Path 1" d="M16.48,0a16.48,16.48,0,0,1,16.48,16.48V89.855A16.48,16.48,0,1,1,0,89.855V16.48A16.48,16.48,0,0,1,16.48,0Z" transform="matrix(0.819, 0.574, -0.574, 0.819, 165.447, 51.709)" fill="#3a3a3a"/>
                        <g id="Logo_2" data-name="Group 1" transform="translate(156.83 77.445)">
                        <g id="Group_2" data-name="Group 2" transform="translate(0 0)">
                        <path id="Rectangle_2" data-name="Rectangle 2" d="M12.184,0h0A24.369,24.369,0,0,1,36.553,24.369V43.8A24.369,24.369,0,0,1,12.184,68.166h0A12.184,12.184,0,0,1,0,55.981v-43.8A12.184,12.184,0,0,1,12.184,0Z" transform="matrix(0.819, 0.574, -0.574, 0.819, 39.098, 0)" fill="#3a3a3a"/>
                        <circle id="Ellipse_1" data-name="Ellipse 1" cx="9.744" cy="9.744" r="9.744" transform="translate(24.776 28.658)" fill="#f1f1f1"/>
                        </g>
                        </g>
                        </g>
                        </g>
                    </svg>
				</div>
            </div>
        </div>
    );
  }
  
  export default LoadingPage;