import * as React from 'react'
import { useRef, useEffect } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/scss'
import '../../../style/Layout/HomePage/Home_4.scss'
import { gsap } from 'gsap'
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';


// Component
import SkillPercent from '../../Component/SkillPercent'

function Home_4(props: any) {
  const { indexPage,Skill,loading } = props
  const tl: any = gsap.timeline()

  function clear() {
    tl.clear()
  }

  function aniIn(){
    clear()
    
    gsap.to(".Home_4-wrapper-title", {
      y: 0,
      opacity: 1,
      duration: .5,
      ease: "power2.out",
      delay: .3
    })
    gsap.to(".mySkillSwiper .swiper-slide", {
      y: 0,
      opacity: 1,
      duration: 0.3,
      stagger: 0.1,
      delay:.6,
    })
    
  }
  function aniOut(){
    clear()
    
    gsap.to(".Home_4-wrapper-title", {
      y: -100,
      opacity: 0,
      duration: .1,
    })
    gsap.to(".mySkillSwiper .swiper-slide", {
      y: 100,
      opacity: 0,
      duration: .1,
    })
    
  }
  useEffect(()=>{
    aniOut()
    setTimeout(
        function (){
          aniIn()
        }
    , 300)
  },[loading])
  
  useEffect(() => {
    if(indexPage === 4){
      aniIn()
    }
    if(indexPage === 3){
      aniOut()
    }
    if(indexPage === 5){
      aniOut()
    }
  },[indexPage]);
  return (
    <div  className="section Home_4">
      <div className="container ">
        <div className="col-11 col-md-10 mr-auto Home_4-wrapper ">
          <div className="Home_4-wrapper-title title-header">
            <p>Coding Languages.</p>
          </div>
          <Swiper
            modules={[Navigation]}
            navigation
            className="mySkillSwiper"
            slidesPerView={2}
            spaceBetween={10}
            breakpoints={{
              // when window width is >= 540px
              640: {
                slidesPerView: 3,
                spaceBetween: 20,
              },
              // when window width is >= 1024px
              1024: {
                slidesPerView: 4,
                spaceBetween: 20,
              },
              // when window width is >= 1366px
              1366: {
                slidesPerView: 5,
                spaceBetween: 20,
              },
            }}
          >
            {Skill.map((items: any, index: any) => {
              return (
                <SwiperSlide>
                  <SkillPercent
                    key={index}
                    items={items}
                  />
                </SwiperSlide>
              )
            })}
          </Swiper>
          <div>

          
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home_4
