import * as React from 'react';
import { Component } from 'react';
import '../../style/Component/EduCerItems.scss'

function EduCerItems(props: any) {
    const {item} = props
    return (
        <div className="block_edu-cer">
            <div className="block_edu-cer_items">
                <div className="block_edu-cer_left">
                    <div className="block_edu-cer_left-img">
                        <img src={item.List_img} alt="" />
                    </div>
                </div>
                <div className="block_edu-cer_right">
                    <div className="block_edu-cer_name">
                        <p>{item.List_name}</p>
                    </div>
                    <div className="block_edu-cer_decs">
                        <p>{item.List_ID}</p>
                    </div>
                    <div className="block_edu-cer_decs">
                        <p>{item.List_time}</p>
                    </div>
                </div>
            </div>
        </div>
    );
  }
  
  export default EduCerItems;