// Library
import * as React from 'react';
import { gsap } from "gsap";
import {useEffect,useRef } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import  '../../../style/Layout/HomePage/Home_2.scss';

// css
import 'swiper/scss';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';

// Component
import InforItems from '../../Component/InforItems';


function Home_2(props: any) {
  const {inforItems,content,indexPage,loading} = props


  const tl: any = gsap.timeline()

  function clear() {
    tl.clear()
  }

  function aniIn(){
    clear()
    
    gsap.to(".information__wrapper--title", {
      x: 0,
      opacity: 1,
      duration: .3,
      delay:.5,
    })
    gsap.to('.myIndexSwiper .swiper-slide', {
      x: 0,
      opacity: 1,
      duration: 0.3,
      stagger: 0.2,
      delay:.4,
    })
    
  }
  function aniOut(){
    clear()
    
    gsap.to(".information__wrapper--title", {
      x: -100,
      opacity: 0,
      duration: .1,
    })
    
    gsap.to(".myIndexSwiper .swiper-slide", {
      x: 100,
      opacity: 0,
      duration: .1,
    })
    
  }
  useEffect(()=>{
    aniOut()
    setTimeout(
        function (){
          aniIn()
        }
    , 300)
  },[loading])
  
  useEffect(() => {
    if(indexPage === 2){
      aniIn()
    }
    if(indexPage === 1){
      aniOut()
    }
    if(indexPage === 3){
      aniOut()
    }
  },[indexPage]);
    return (
      <div className="section Home_2">
        <section id="dp-information">
					<div className="information">
						<div className="container">
							<div className="information__wrapper">    
								<div className="information__wrapper--title title-header">
									<p>Informations.</p>
								</div>
								<div className="information__wrapper--list">
                  <Swiper
                    modules={[Navigation]}
                    navigation
                    className="myIndexSwiper"
                    slidesPerView={1}
                    spaceBetween={20}
                    breakpoints={{
                      // when window width is >= 540px
                      640: {
                        // width: 768,
                        slidesPerView: 2,
                        spaceBetween: 20,
                      },
                      // when window width is >= 1366px
                      1366: {
                        slidesPerView: 3,
                        spaceBetween: 20,
                      },
                    }}
                  >
                  {
                    inforItems.map((item:any, index:any) => (
                      <SwiperSlide key={index}>
                        <InforItems
                          key={index}
                          item={item}
                        />
                      </SwiperSlide>
                      
                    ))
                  }
                  </Swiper>
								</div>
							</div>
						</div>
					</div>
				</section>
      </div>
    );
  }
  
  export default Home_2;