import * as React from 'react';
import { Component } from 'react';
import '../../style/Component/EduCerDetails.scss'

function EduCerDetails(props: any) {
    const {item,MajorRender} = props
    return (
        <div className="edu-cer_right-items">
            <div className="right-items_name">
                <p>{item.List_name}</p>
            </div>
            {

                MajorRender(item.List_major)
            }
            <div className="right-items_decs">
                <p>{item.List_time}</p>
            </div>
        </div>
    );
  }
  
  export default EduCerDetails;