/* eslint-disable react/jsx-pascal-case */
import * as React from "react";
import Home_1 from "../Layout/HomePage/Home_1";
import Home_2 from "../Layout/HomePage/Home_2";
import Home_3 from "../Layout/HomePage/Home_3";
import Home_4 from "../Layout/HomePage/Home_4";
import Home_5 from "../Layout/HomePage/Home_5";
import "../../style/Page/Home-Page.scss";

function HomePage(props: any) {
  const {
    products_recommendList,
    inforItems,
    List_EduCer,
    fullpageApi,
    EduEerDetails,
    indexPage,
    Skill,
    Exp,
    loading,
    fp,
  } = props;

  return (
    <>
      <Home_1
        content={"everything is in the conceptcube"}
        indexPage={indexPage}
        loading={loading}
        fp={fp}
      />
      <Home_2
        products_recommendList={products_recommendList}
        content={"Creative, Fun and- Enjoyable work Impressive"}
        indexPage={indexPage}
        inforItems={inforItems}
        loading={loading}
      />
      <Home_3
        indexPage={indexPage}
        List_EduCer={List_EduCer}
        EduEerDetails={EduEerDetails}
        fullpageApi={fullpageApi}
        loading={loading}
      />
      <Home_4 indexPage={indexPage} Skill={Skill} loading={loading} />
      <Home_5 Exp={Exp} indexPage={indexPage} loading={loading} />
    </>
  );
}

export default HomePage;
