import React, { useEffect } from 'react';
import {useRef} from 'react';
import ReactDOM from 'react-dom';
// import "fullpage.js/vendors/scrolloverflow";
import ReactFullpage from '@fullpage/react-fullpage';

// pages-------
import HomePage from '../Page/Home-Page'

import { useState } from 'react';
import { gsap } from "gsap";





function FullpageWrapper(props) {
  const {changeSvg} = props
  const [indexPage, setIndexPage] = useState(1);
  const fullpage = useRef("")
  const {loading} =props
  const [fp, setFp] =useState(false)

  const [windowDimention , setWidthHeight] = useState({
    Width: window.innerWidth,
    Height: window.innerHeight,
  })

  const DetectSize = () =>{
    setWidthHeight({
      Width: window.innerWidth,
      Height: window.innerHeight,
    })
  }


  useEffect(()=>{
    if(loading){
      window.addEventListener('resize',DetectSize)
     
      return () =>{
        window.removeEventListener('resize',DetectSize)
        
      }
      
    }
  },[windowDimention])

 
  return (
    <ReactFullpage
    
    scrollingSpeed = {900}
    navigation
    afterLoad={(origin, currentIndex, direction) => {
      changeSvg(currentIndex.index +1)
      
    }}
    onLeave={(origin, destination, direction) => {
      setIndexPage(destination.index + 1)
      changeSvg(destination.index + 1)
    }}
    normalScrollElements={'.edu-cer_right'}
    
    render={({ state, fullpageApi }) => {
      // FPAPI =fullpageApi
      const {products_recommendList,inforItems,title,List_EduCer,EduEerDetails,Skill,Exp} = props;
      // console.log( fullpageApi.getActiveSlide());
      // page = Page
      if(loading){

        if(windowDimention.Width < 1024 || windowDimention.Height < 540){
          fullpageApi.destroy("all")
        }
      }
      return (
        <>
          
          <ReactFullpage.Wrapper
                >
                    
                    <HomePage 
                      products_recommendList={products_recommendList}
                      inforItems={inforItems}
                      title={title}
                      List_EduCer={List_EduCer}
                      fullpageApi={fullpageApi}
                      EduEerDetails={EduEerDetails}
                      indexPage={indexPage}
                      Skill={Skill}
                      Exp={Exp}
                      loading={loading}
                      fp={fp}
                    />
              </ReactFullpage.Wrapper>
            </>
         
      );
    }}
  />
  );
}

export default FullpageWrapper